.profile{
    display: flex;
    align-items: center;
    position: relative;
}

.img-d{
  width: 35px;
border-radius: 50%;
border: 1px solid rgb(233, 229, 229);
}

.info{
    margin-left: 10px;
}

.info span{
    color: #fff;
}
.info p {
    color: #fff;
    opacity: 50%;
}
.content{
    position: relative;
    color: hsl(210, 46%, 95%);

}
.heading {
    padding: 10px 0px;
    z-index: 999;

}

.heading h1{
    font-size: 18px;
    font-weight: 500;  
}
.para {
    padding-bottom: 10px;
}
.para p{
    font-size: 13px;
    opacity: 70%;
    font-weight: none;
}
.Kiraname, .kiraVer{
    color: #000 !important;
}
 .kiraVer{
    opacity: 50%;
}


.img-q{
    width: 100px;
    position: absolute;
    right: 40px;
    top: -20px;
    z-index: inherit;
}


@media screen and (max-width: 385px) {

    .img-q{
        width: 100px;
        position: absolute;
        right: 0px;
        top: -20px;
        z-index: inherit;
    }
}