/* Import fonts family  */


/* css Reset  */
*{
  padding: 0;
margin: 0;
box-sizing: border-box;
font-family: 'Barlow Semi Condensed', sans-serif;
}

/* setting background in body  */
body {
padding: 0;
margin: 0;
box-sizing: border-box;
min-height: 100vh;
background-color: hsl(210, 46%, 95%);
display: flex;
align-items: center;
justify-content: center;
}





