/* using grid  */



.allUser {
  display: grid;
  grid-template-columns: repeat(6, 110px);
  grid-template-rows: repeat(6, 80px);
  grid-gap: 1rem 1.2rem;

  padding: 10vh;

}

.user {
  background-color: gray;
  padding: 25px;
border-radius: 10px;

}

.user-1 {
  grid-column: 1 / span 4;
  grid-row: 1 / span 3;
  width: 500px;
  background-color: hsl(263, 55%, 52%);


}

.user-2 {

  grid-column: 5/ span 6;
  grid-row: 1 / span 3;
  width: 240px;
  background-color: hsl(217, 19%, 35%);

}

.user-3 {

  grid-column: 7 / span 8;
  grid-row: 1 / span 6;
  width: 250px;
  background-color: hsl(0, 0%, 100%);
}

.user-4 {
  grid-column: 1 / span 2;
  grid-row: 4 / span 5;
  height: 275px;
  background-color: hsl(0, 0%, 100%);

}

.user-5 {
  grid-column: 3 / span 4;
  grid-row: 4 / span 5;
  height: 275px;
  background-color: hsl(219, 29%, 14%);

}

@media screen and (max-width: 1050px) {
  .allUser {
    display: grid;
    grid-template-columns: repeat(1, 375px);
    grid-template-rows: repeat(6, 1fr);
    grid-gap: 1rem 1rem;
    /* position: over; */

  }

  .user-1 {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    width: 350px;
    background-color: hsl(263, 55%, 52%);
    /* height: 600px; */
  }

  .user-2 {
    width: 350px;
    grid-column: 1 / span 1;
    grid-row: 2 / span 2;
    background-color: hsl(217, 19%, 35%);
    /* height: 300px; */
    height: 300px;

  }

  .user-3 {
    width: 350px;
    grid-column: 1 / span 1;
    grid-row: 5 / span 5;
    background-color: hsl(0, 0%, 100%);
    /* height: 370px; */
    height: 370px;
  }

  .user-4 {
    grid-column: 1 / span 1;
    grid-row: 3 / span 3;
    background-color: hsl(0, 0%, 100%);
    /* height: 250px; */

    width: 350px;

  }

  .user-5 {
    grid-column: 1 / span 1;
    grid-row: 4 / span 4;
    background-color: hsl(219, 29%, 14%);
    width: 350px;
    height: 330px;

  }


 
}


  @media screen and (max-width: 385px) {
    .allUser {
      display: grid;
      grid-template-columns: repeat(1, auto);
      grid-template-rows: repeat(6, 1fr);
      grid-gap: 1rem 1rem;
      /* position: over; */
  
    }
    .user{
      width: 95vw;
    }

