      grid-column: 1 / span 1;
      grid-row: 2 / span 2;
      background-color: hsl(217, 19%, 35%);
      height: 300px;
  
    }
  
    .user-3 {
      grid-column: 1 / span 1;
      grid-row: 5 / span 5;
      background-color: hsl(0, 0%, 100%);
      height: 500px;
    }
  
    .user-4 {
      grid-column: 1 / span 1;
      grid-row: 3 / span 3;
      background-color: hsl(0, 0%, 100%);
      /* height: 250px; */
  
      height: 250px;
    }
  
    .user-5 {
      grid-column: 1 / span 1;
      grid-row: 4 / span 4;
      background-color: hsl(219, 29%, 14%);
      height: 335px;
    }

  }



